import axios from "axios";
import React, { Fragment, useEffect, useState } from "react";
import { IoMdInformationCircleOutline } from "react-icons/io";
import { MdLocationOn, MdSearch } from "react-icons/md";
import Loading from "../../../components/Loading/Loading";
import Modal, { ModalSize, Themes } from "../../../components/Modal/Modal";
import { course } from "../../../interfaces/course";
import { LocationAPI } from "../../../interfaces/location";
import { API } from "../../../utils/api";
import searchData from "../../../utils/search";

export interface examRoomInterface {
  center_id: string;
  isUsed: boolean;
  room_type: string;
  room_number: number;
  capacity: number;
  _id: string;
  occupied: number;
}

export interface LocationTableInterface {
  province_id: string;
  province_name: string;
  district_id: string;
  district_name: string;
  center_id: string;
  center_name: string;
  rooms: examRoomInterface[];
  capacity: number;
  occupied: number;
}

interface ChooseExamCenterProps {
  addLocation: (
    course_id: string,
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string
  ) => void;
  positionCourses: course;
  closeModal: () => any;
}

export const findCenterCapacity = (rooms: examRoomInterface[]) => {
  let sum = 0;
  for (let i in rooms) {
    sum = sum + rooms[i].capacity;
  }
  return sum;
};

export const LocationTable = (
  LocationData: LocationAPI[]
): LocationTableInterface[] | [] => {
  let locationTemp: LocationTableInterface[] = [];
  for (let p in LocationData) {
    let districts = LocationData[p].districts;
    for (let d in districts) {
      let centers = districts[d].centers;
      for (let c in centers) {
        let rooms = centers[c].rooms;
        let capacity = 0;
        let occupied = 0;
        if (rooms.length > 0) {
          for (let x in rooms) {
            if (rooms[x].room_number !== 0) {
              capacity = capacity + rooms[x].capacity;
              occupied = occupied + rooms[x].occupied;
            }
          }
          locationTemp.push({
            province_id: LocationData[p]._id,
            province_name: LocationData[p].full_name,
            district_id: districts[d]._id,
            district_name: districts[d].district_name,
            center_id: centers[c]._id,
            center_name: centers[c].center_name,
            rooms: rooms,
            capacity: capacity,
            occupied: occupied,
          });
        }
      }
    }
  }
  return locationTemp;
};

const ChooseExamCenter = (props: ChooseExamCenterProps) => {
  const [search, setSearch] = useState<string | "">("");
  const [locations, setLocations] = useState<LocationAPI[] | [] | null>(null);
  const [selectedProvince, setSelectedProvince] = useState<string | null>(null);
  const [warning, setWarning] = useState<{
    show: boolean;
    value: boolean;
    item: any;
  }>({
    show: false,
    value: false,
    item: "",
  });

  const LoadLocations = async () => {
    try {
      const res = await axios.get<LocationAPI[]>(
        `${API.APPLICATION}/roomlocation/course/${props.positionCourses.course_id}`
      );
      if (res.status === 200) {
        if (locations === null || locations.length === 0) {
          setLocations(res.data);
        }
      }
      // console.log("Locations: ", locations);
    } catch (error: any) {
      console.log("Locations err: ", { ...error });
    }
  };

  const sortLocations = (MyLocations: LocationTableInterface[]) => {
    let data: LocationTableInterface[] = MyLocations;
    for (let i = 0; i < data.length; i++) {
      for (let j = 0; j < data.length - i - 1; j++) {
        let check1 = data[j + 1].capacity - data[j + 1].occupied;
        let check2 = data[j].capacity - data[j].occupied;
        if (check1 > check2) {
          [data[j + 1], data[j]] = [data[j], data[j + 1]];
        }
      }
    }
    return data;
  };

  useEffect(() => {
    LoadLocations();
  });
  return (
    <Fragment>
      <div className="w-full">
        <div className="flex flex-row items-center">
          <div>
            <MdLocationOn className="text-5xl text-blue-600" />
          </div>
          <div className="flex flex-col justify-start">
            <span className="text-xl font-bold text-blue-600">
              Exam: {props.positionCourses.course_code}-
              {props.positionCourses.course_name}
            </span>
            <span className="text-sm text-gray-500">
              Choose location for the exam
            </span>
          </div>
        </div>

        <div className="flex flex-col md:flex-row px-3 gap-3 my-3 md:items-center w-full">
          <div>
            <span className="text-gray-700 font-bold">Filter by location</span>
          </div>
          <div className="flex flex-col md:flex-row gap-3 w-full md:w-2/3">
            <select
              onChange={(e: React.ChangeEvent<HTMLSelectElement>) =>
                setSelectedProvince(e.target.value)
              }
              className="input-white bg-white border border-gray-500 w-1/3"
            >
              <option value="">Select province</option>
              {locations !== null &&
                locations.length > 0 &&
                locations.map((item, i) => (
                  <option key={i + 1} value={item._id}>
                    {item.province_name}
                  </option>
                ))}
            </select>
            <input
              onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
                setSearch(e.target.value)
              }
              type="text"
              placeholder="District or center name"
              className="input-white bg-white border border-gray-500 2/3"
            />
          </div>
        </div>
        <div className="w-full overflow-scroll">
          <table className="w-full overflow-x-auto">
            <thead>
              <tr className="border-b-2 border-blue-600 py-4 bg-blue-100 text-blue-900 text-left">
                <th className="py-3 px-2 text-center font-bold">#</th>
                <th className="py-3 px-2 font-bold">Province</th>
                <th className="py-3 px-2 font-bold">District</th>
                <th className="py-3 px-2 font-bold">Center</th>
                <th className="py-3 px-2 font-bold">Capacity</th>
                <th className="py-3 px-2 font-bold">Occupied</th>
                <th className="py-3 font-bold px-3 text-right">Action</th>
              </tr>
            </thead>

            {locations === null ? (
              <tbody>
                <tr className="pt-6">
                  <td colSpan={7}>
                    <Loading />
                  </td>
                </tr>
              </tbody>
            ) : locations.length === 0 ? (
              <tbody>
                <tr>
                  <td
                    colSpan={7}
                    className="flex flex-col justify-center items-center text-gray-400 pt-4"
                  >
                    <MdSearch className="text-7xl" />
                    <span className="text-3xl">No result found</span>
                  </td>
                </tr>
              </tbody>
            ) : (
              <tbody>
                {searchData(
                  selectedProvince !== null
                    ? LocationTable(locations).filter(
                        (itm) => itm.province_id === selectedProvince
                      )
                    : sortLocations(LocationTable(locations)),
                  search
                ).map((item, i) => (
                  <tr
                    onClick={() => {
                      if (item.capacity <= item.occupied) {
                        setWarning({
                          show: true,
                          value: false,
                          item: item,
                        });
                      } else {
                        props.addLocation(
                          props.positionCourses.course_id,
                          props.positionCourses.course_name,
                          props.positionCourses.course_code,
                          item,
                          props.positionCourses.course_id
                        );
                        props.closeModal();
                      }
                    }}
                    title="Click to choose this location"
                    key={i + 1}
                    className={`group gap-3 rounded md:rounded-none  px-3 mx-2 md:mx-0 md:px-0 bg-white mb-2 md:mb-0 border md:border-0 md:border-b text-gray-700 border-gray-300 py-1 text-left cursor-pointer ${
                      item.capacity <= item.occupied
                        ? "hover:text-yellow-800 hover:border-yellow-600 hover:bg-yellow-100"
                        : "hover:text-blue-800 hover:border-blue-600 hover:bg-blue-100"
                    } text-sm hover:font-bold`}
                  >
                    <td className="px-2 text-center font-bold flex items-center justify-center">
                      <div
                        className={`${
                          item.capacity <= item.occupied
                            ? "bg-yellow-100 text-yellow-600 group-hover:bg-yellow-600"
                            : "bg-blue-100 text-blue-600 group-hover:bg-blue-600"
                        } group-hover:text-white rounded-full w-8 h-8 flex items-center justify-center my-1`}
                      >
                        {i + 1}
                      </div>
                    </td>
                    <td className="px-2">{item.province_name}</td>
                    <td className="px-2">{item.district_name}</td>
                    <td className="px-2">{item.center_name}</td>
                    <td className="px-2">{item.capacity}</td>
                    <td className="px-2">{item.occupied}</td>
                    <td className=" px-3">
                      {props.positionCourses !== undefined && (
                        <button
                          className={`h-full px-2 py-1 float-right ${
                            item.capacity <= item.occupied
                              ? "bg-yellow-100 text-yellow-700 group-hover:bg-yellow-600"
                              : "bg-blue-100 text-blue-700 group-hover:bg-blue-600"
                          } group-hover:text-white rounded`}
                        >
                          Choose
                        </button>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            )}
          </table>
        </div>
      </div>
      {/* Warning Popup */}
      {warning.show === true && (
        <Modal
          backDrop={true}
          theme={Themes.default}
          close={() =>
            setWarning({
              show: false,
              value: false,
              item: "",
            })
          }
          backDropClose={true}
          widthSizeClass={ModalSize.medium}
          displayClose={true}
          padding={{
            title: true,
            body: true,
            footer: false,
          }}
          title={
            <div className="flex flex-row items-center gap-3 mr-5">
              <IoMdInformationCircleOutline className="text-5xl text-yellow-800 text-center" />
              <span className="text-2xl text-yellow-800 font-extrabold">
                Warning
              </span>
            </div>
          }
        >
          <div className="mb-4 -mt-2 text-base text-gray-900 flex flex-col px-1">
            <span>Dear Applicant,</span>
            <span className="text-yellow-800 underline font-bold">
              The selected center is fully occupied.
            </span>
            <div>
              We advise you to choose another center of exam. However, you can
              continue with the selected center, but you will get arranged at
              the center of exam which may delay/affect the start of
              examination. Good Luck
            </div>
          </div>
          <div className="flex flex-row items-center justify-between">
            <div
              title="Cancel this location, choose another one"
              onClick={() =>
                setWarning({
                  show: false,
                  value: false,
                  item: "",
                })
              }
              className="btn bg-gray-100 text-gray-600 cursor-pointer"
            >
              Cancel
            </div>
            <div
              title="Click to select the location anyway!"
              onClick={() => {
                props.addLocation(
                  props.positionCourses.course_id,
                  props.positionCourses.course_name,
                  props.positionCourses.course_code,
                  warning.item,
                  props.positionCourses.course_id
                );
                setWarning({
                  show: false,
                  value: false,
                  item: "",
                });
                props.closeModal();
              }}
              className="btn bg-blue-100 text-blue-600 hover:bg-blue-600 hover:text-white cursor-pointer"
            >
              Continue anyway
            </div>
          </div>
        </Modal>
      )}
    </Fragment>
  );
};

export default ChooseExamCenter;
